<template>
  <div>
    <div class="vs-row flex hover mb-base">
      <div class="nv-col md:w-1/2 w-full p-2">
        <p class="info-row">
          <span>{{ $t('STREET') }}:</span>
          <span>{{ beneficiary.address1 | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('DISTRICT') }}:</span>
          <span>{{ beneficiary.district | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('CITY') }}:</span>
          <span>{{ beneficiary.city | placeholder('-') }}</span>
        </p>
        <p class="info-row" v-if="beneficiary.typeBehaviourEnumId === 'BEN_TYPE_OVERSEAS'">
          <span>{{ $t('COUNTRY') }}:</span>
          <span>{{ beneficiary.countryGeoName | placeholder('-') }}</span>
        </p>
      </div>
      <div class="nv-col md:w-1/2 w-full p-2">
        <p class="info-row">
          <span>{{ $t('PROVINCE') }}:</span>
          <span>{{ beneficiary.province | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('POSTAL_CODE') }}:</span>
          <span>{{ beneficiary.postalCode | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('POST_OFFICE_BOX') }}:</span>
          <span>{{ beneficiary.postOfficeBox | placeholder('-') }}</span>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import BeneficiaryService from '@/services/beneficiary/BeneficiaryService'

export default {
  name: 'beneficiary-address',
  mixins: [LoadingMixin],
  props: {
    beneficiaryId: {
      type: String,
      required: true
    }
  },
  data: function () {
    return {
      beneficiary: {}
    }
  },
  methods: {
    async load () {
      let self = this
      BeneficiaryService.beneficiaries({
        beneficiaryId: this.beneficiaryId
      }).then(function (response) {
        self.beneficiary = response.data.beneficiaryList[0] || {}
      })
    }
  },
  mounted () {
    this.load()
  },
  watch: {
    $route: function (to, from) {
      this.beneficiaryId = this.$route.params.id
      this.load()
    }
  }
}
</script>
