  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>
    <nv-card>
      <div class="flex flex-wrap-reverse items-center mb-base" v-if="viewOnly === false">
        <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="modify" color="primary" type="filled" icon="fa-pen" icon-pack="far" v-if="$acl.check('isBeneficiaryAll')">{{ $t('MODIFY') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="transfer" color="primary" type="filled" icon="fa-exchange" icon-pack="far" v-if="$acl.check('isBeneficiaryAll')">{{ $t('TRANSFER') }}</vs-button>
        <vs-button class="p-3 mr-4" v-if="beneficiary.statusDescription === 'Active'" @click="deleteBeneficiaryConfirm(beneficiary)" color="danger" type="filled" icon="fa-trash-alt" icon-pack="far">{{ $t('DELETE') }}</vs-button>
      </div>

      <div id="loading-container" class="nv-row row hover vs-con-loading__container">
        <template v-if="beneficiary.typeBehaviourEnumId === 'BEN_TYPE_BANK'">
          <div class="nv-col mb-base w-full">
            <p class="info-row">
              <span>{{ $t('ID') }}:</span>
              <span>{{ beneficiary.beneficiaryId | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('BENEFICIARY_NAME') }}:</span>
              <span>{{ beneficiary.beneficiaryName | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('NICKNAME') }}:</span>
              <span>{{ beneficiary.nickname | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('BENEFICIARY_TYPE') }}:</span>
              <span>{{ beneficiary.typeName | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('STATUS') }}:</span>
              <span>{{ beneficiary.statusDescription | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('ACCOUNT_NUMBER') }}:</span>
              <span>{{ beneficiary.accountNumber | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('CREATION_DATE') }}:</span>
              <span>{{ beneficiary.creationDate | formatUnixDate }}</span>
            </p>
          </div>
        </template>

        <template v-if="beneficiary.typeBehaviourEnumId === 'BEN_TYPE_LOCAL'">
          <div class="nv-col mb-base w-full">
            <p class="info-row">
              <span>{{ $t('ID') }}:</span>
              <span>{{ beneficiary.beneficiaryId | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('BENEFICIARY_NAME') }}:</span>
              <span>{{ beneficiary.beneficiaryName | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('NICKNAME') }}:</span>
              <span>{{ beneficiary.nickname | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('BENEFICIARY_TYPE') }}:</span>
              <span>{{ beneficiary.typeName | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('STATUS') }}:</span>
              <span>{{ beneficiary.statusDescription | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('IBAN') }}:</span>
              <span>{{ beneficiary.iban | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('BANK') }}:</span>
              <span>{{ beneficiary.bankName | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('SWIFT_CODE') }}:</span>
              <span>{{ beneficiary.swiftCode | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('CREATION_DATE') }}:</span>
              <span>{{ beneficiary.creationDate | formatUnixDate }}</span>
            </p>
          </div>
        </template>

        <template v-if="beneficiary.typeBehaviourEnumId === 'BEN_TYPE_OVERSEAS'">
          <div class="nv-col mb-base w-full">
            <p class="info-row">
              <span>{{ $t('ID') }}:</span>
              <span>{{ beneficiary.beneficiaryId | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('BENEFICIARY_NAME') }}:</span>
              <span>{{ beneficiary.beneficiaryName | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('NICKNAME') }}:</span>
              <span>{{ beneficiary.nickname | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('BENEFICIARY_TYPE') }}:</span>
              <span>{{ beneficiary.typeName | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('STATUS') }}:</span>
              <span>{{ beneficiary.statusDescription | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('BANK') }}:</span>
              <span>{{ beneficiary.bankName | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('SWIFT_CODE') }}:</span>
              <span>{{ beneficiary.swiftCode | placeholder('-') }}</span>
            </p>
            <p class="info-row" v-if="beneficiary.accountNumber">
              <span>{{ $t('ACCOUNT_NUMBER') }}:</span>
              <span>{{ beneficiary.accountNumber | placeholder('-') }}</span>
            </p>
            <p class="info-row" v-if="beneficiary.iban">
              <span>{{ $t('IBAN') }}:</span>
              <span>{{ beneficiary.iban | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('PREFERRED_CURRENCY') }}:</span>
              <span>{{ beneficiary.preferredCurrencyAbbreviation | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('CREATION_DATE') }}:</span>
              <span>{{ beneficiary.creationDate | formatUnixDate }}</span>
            </p>
          </div>
        </template>
      </div>

      <vs-tabs v-if="beneficiary.typeBehaviourEnumId !== 'BEN_TYPE_BANK'">
        <vs-tab :label="$t('ADDRESSES')">
          <beneficiary-address @show-alert="showAlert" :beneficiaryId="beneficiaryId" :viewOnly="viewOnly"/>
        </vs-tab>
      </vs-tabs>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import BeneficiaryService from '@/services/beneficiary/BeneficiaryService'
import BeneficiaryAddress from '@/views/beneficiary/beneficiary-root/beneficiary-view/BeneficiaryAddress'

export default {
  name: 'beneficiary-view',
  mixins: [AlertMixin, LoadingMixin],
  components: {
    BeneficiaryAddress
  },
  props: {
    beneficiaryId: {
      type: String,
      default: null
    },
    viewOnly: {
      type: Boolean,
      default: false
    }
  },
  data: function () {
    return {
      beneficiary: {}
    }
  },
  methods: {
    async load () {
      let self = this
      this.startLoading('loading-container')
      BeneficiaryService.beneficiaries({
        beneficiaryId: this.beneficiaryId
      }).then(function (response) {
        self.stopLoading('loading-container')
        self.beneficiary = response.data.beneficiaryList[0] || {}
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    },
    modify () {
      this.$router.push({ name: 'ModifyBeneficiary', params: { id: this.beneficiaryId } })
    },
    transfer () {
      this.$router.push({ name: 'FundsTransferBeneficiary', params: { id: this.beneficiaryId } })
    },
    deleteBeneficiaryConfirm (beneficiary) {
      this.$vs.dialog({
        type: 'confirm',
        color: 'danger',
        title: this.$t('CONFIRMATION'),
        text: this.$t('CONFIRM_DELETE'),
        accept: this.deleteBeneficiary.bind(this, beneficiary),
        acceptText: this.$t('YES'),
        cancelText: this.$t('NO')
      })
    },
    deleteBeneficiary: function (beneficiary) {
      let self = this
      BeneficiaryService.deleteBeneficiary({
        beneficiaryId: beneficiary.beneficiaryId
      }).then(function (response) {
        if (response.status === 200) {
          self.$vs.notify({
            color: 'success',
            title: self.$t('CONFIRMATION'),
            text: self.$t('REQUEST_SUBMITTED')
          })
          self.$router.push({ name: 'BrowseBeneficiaries' })
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    }
  },
  mounted () {
    this.load()
  },
  watch: {
    $route: function (to, from) {
      this.load()
    },
    beneficiaryId: function () {
      this.load()
    }
  }
}
</script>
